<template>
	<el-aside width="200px" class="xlsidebar" style="background-color: rgb(238, 241, 246)">
	  <el-menu :default-openeds="[meauList[0].navTitle]" :default-active="this.$route.path" router>
			<el-submenu :index="tab.navTitle"  v-for="tab in meauList" >
				<template slot="title"><i :class="tab.iconClass"></i>{{tab.navTitle}}</template>
				<el-menu-item v-for="mychild in tab.navchild" v-if="!mychild.navchild" :index="mychild.navhref">{{mychild.navTitle}}</el-menu-item>
				<el-submenu :index="mychild.navTitle" v-for="mychild in tab.navchild" v-if="mychild.navchild">
					<template slot="title">{{mychild.navTitle}}</template>
					<el-menu-item v-for="mychildchild in mychild.navchild"  :index="mychildchild.navhref">{{mychildchild.navTitle}}</el-menu-item>
				</el-submenu>
			</el-submenu>
		</el-menu>
	</el-aside>
</template>

<script>
export default {
  name: 'Sidebar',
  data () {
    return {
    	meauList: [{
	    		navTitle:"导航一",
	    		iconClass:"el-icon-message",
	    	 	navchild:[
	    	 		{
	    	 			navTitle:"首页",
	    	 			navhref:"/index/proindex"
	    	 		},
	    	 		{
	    	 			navTitle:"表单",
	    	 			navhref:"/index/aa"
	    	 		},
	    	 		{
	    	 			navTitle:"图标",
	    	 			navhref:"/Icon"
	    	 		},
	    	 		{
	    	 			navTitle:"选项4",
	    	 			navchild:[
			    	 			{
			    	 			navTitle:"选项4-1",
			    	 			navhref:"/form2"
			    	 		}
	    	 			]
	    	 		}
	    	 	]
	    	},
	    	{
	    		navTitle:"导航二",
	    		iconClass:"el-icon-menu",
	    	 	navchild:[
	    	 		{
	    	 			navTitle:"选项2-1",
	    	 			navhref:"/form5"
	    	 		},
	    	 		{
	    	 			navTitle:"选项2-2",
	    	 			navhref:"/form6"
	    	 		},
	    	 		{
	    	 			navTitle:"选项3-3",
	    	 			navhref:"/form7"
	    	 		},
	    	 		{
	    	 			navTitle:"选项3-3",
	    	 			navhref:"/form7"
	    	 		}
	    	 	]
	    	}
    	]
    }
  }
}
</script>
<style scoped="scoped">
	.el-aside {
    background-color: #D3DCE6;
    color: #333;
    line-height: 200px;
  }
	.xlsidebar>ul{
  	position: fixed;
    top: 60px;
    width: 199px;
	  }
	.xlsidebar:before{
		content: "";
	    display: block;
	    width: inherit;
	    position: absolute;
	    top: 0px;
	    bottom: 0;
	    z-index: -1;
	    background-color: inherit;
	}
</style>




