import Vue from 'vue'
import Router from 'vue-router'
import Eorr404 from '@/components/Eorr404'
import Login from '@/views/login'
import Index from '@/views/index'
import ProcIndex from '@/views/project/procIndex'

Vue.use(Router)


export default new Router({
		base: __dirname,
  	routes: [
  	{
      path: '/',
      component: Login
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/index',
      name: 'Index',
      component: Index,
      children:[
      	{
      		path:'proindex',
      		name: 'ProcIndex',
      		component:ProcIndex
      	},
      	{
      		path:'*',
      		component:Eorr404
      	}
      ]
    },
    {
      path: '/*',
      name: 'Eorr404',
      component: Eorr404
    }
  ]
})





